body {
  margin: 0px; }

#demo {
  position: absolute;
  width: 100%;
  height: 100%; }
  #demo #mouse,
  #demo #future-mouse {
    position: absolute;
    margin-top: -5px;
    margin-left: -5px;
    border: 5px solid;
    border-radius: 50%; }
  #demo #future-mouse {
    -webkit-transition: 0.1s;
    -o-transition: 0.1s;
    transition: 0.1s; }



